<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.frameNo" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.carNo" placeholder=""></el-input>
				</el-form-item>
				
				<el-form-item>
					<el-button type="primary" v-on:click="getContents"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="contents" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column label=""  with="auto"  		prop="carNo"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="brand"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="frameNo"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="engineNo"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="idNo"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="owerName"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="licenseDate"></el-table-column>
			<el-table-column label=""  with="auto"  			 prop="addr"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="phoneNum"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="company"></el-table-column>
			<el-table-column label=""  with="auto"  	prop="viPolicyNo"></el-table-column>
			<el-table-column label=""  with="auto"  	    prop="finalInsDate"></el-table-column>
			<el-table-column label=""  with="auto"  	prop="useVal"></el-table-column>
			<el-table-column label=""  with="auto"  	prop="viAmount"></el-table-column>
			<el-table-column label=""  with="auto"  	prop="viDiscount"></el-table-column>
			<el-table-column label=""  with="auto"  	prop="payViAmt"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="ciAmount"></el-table-column>
			<el-table-column label=""  with="auto"  		prop="vvt"></el-table-column>
			<el-table-column label=""  with="auto"  			 prop="content"></el-table-column>

			<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="carNo">
					<el-input placeHolder="" v-model="editForm.carNo">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="brand">
					<el-input placeHolder="" v-model="editForm.brand">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="frameNo">
					<el-input placeHolder="" v-model="editForm.frameNo">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="engineNo">
					<el-input placeHolder="" v-model="editForm.engineNo">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="idNo">
					<el-input placeHolder="" v-model="editForm.idNo">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="owerName">
					<el-input placeHolder="" v-model="editForm.owerName">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="licenseDate">
					<el-input placeHolder="" v-model="editForm.licenseDate">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="addr">
					<el-input placeHolder="" v-model="editForm.addr">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="phoneNum">
					<el-input placeHolder="" v-model="editForm.phoneNum">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="company">
					<el-input placeHolder="" v-model="editForm.company">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="	viPolicy">
					<el-input placeHolder="" v-model="editForm.viPolicyNo">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="finalInsDate">
					<el-input placeHolder="" v-model="editForm.finalInsDate">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="	useVal">
					<el-input placeHolder="" v-model="editForm.useVal">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="viAmount">
					<el-input placeHolder="" v-model="editForm.viAmount">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="viDiscount">
					<el-input placeHolder="" v-model="editForm.viDiscount">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="	payViAmt">
					<el-input placeHolder="" v-model="editForm.payViAmt">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="ciAmount">
					<el-input placeHolder="" v-model="editForm.ciAmount">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="vvt">
					<el-input placeHolder="" v-model="editForm.vvt">
					</el-input>
				</el-form-item>
				<el-form-item label="" prop="content">
					<el-input placeHolder="" v-model="editForm.content">
					</el-input>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-upload
					class="filter-item"
					name="file"
					action="/data/in"
					:on-error="uploadError"
					:on-success="uploadSuccess"
					:before-upload="beforUpload"
					:limit="1"
					accept=".xlsx,.xls"
					:show-file-list="false"
					:file-list="fileList">
				<el-button  style="margin-left: 10px;" icon="el-icon-edit" type="primary"></el-button>
				<div slot="tip" class="el-upload__tip">Excel10M</div>
			</el-upload>
		</el-dialog>
	</section>
</template>

<script>
    import util from '../../common/js/util';
    import NProgress from 'nprogress';
	import {addData, editData, listData} from '../../api/api';

    export default {
		data:function() {
			return {
				filters: {
                    frameNo:null,
					carNo:null
				},
                contents: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//
				fileList:[],
				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
                    id: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
				},
				//
				editForm: {
					id: null,
					/***/
					carNo:"",
					/***/
					brand:"",
					/***/
					frameNo:"",
					/***/
					engineNo:"",
					/***/
					idNo:"",
					/***/
					owerName:"",
					/***/
					licenseDate:"",
					/***/
					addr:"",
					/***/
					phoneNum:"",
					/***/
					company:"",
					/***/
					viPolicyNo:"",
					/***/
					finalInsDate:"",
					/***/
					useVal:"",
					/***/
					viAmount:"",
					/***/
					viDiscount:"",
					/***/
					payViAmt:"",
					/***/
					ciAmount:"",
					/***/
					vvt:"",
					/***/
					content:""
				},
				addFormVisible: false,//
				addLoading: false

			}
		},
		methods: {
			
			handleCurrentChange:function(val) {
				this.page = val;
				this.getContents();
			},
			//
			getContents:function() {
				let params = {
					pageNum: this.page - 1,
                    pageSize:10,
					cardNo: this.filters.carNo || null,
					frameNo: this.filters.frameNo || null
				};
				this.listLoading = true;
				NProgress.start();
                listData(params , this).then((res) => {
                    if(!res){
                        return;
					}
					this.total = res.total;
					this.contents = res.data.data;
					this.listLoading = false;
					NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
                var _this = this;
                util.Msg.warning(_this , null ,function () {
                    _this.listLoading = true;
                    NProgress.start();
                    let para = { ids: row.id };
                    delData(para).then(function() {
                        _this.listLoading = false;
                        NProgress.done();
                        util.Msg.success(_this);
                        _this.getContents();
                    });
                });
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
			},
			//
			editSubmit: function () {
                var _this = this;
				this.$refs.editForm.validate((valid) => {
					if (valid) {
                        util.Msg.confirm(_this , null ,function () {
                            _this.editLoading = true;
                            NProgress.start();
                            let para = Object.assign({}, _this.editForm);
                            editAcct(para).then(function(){
                                _this.editLoading = false;
                                NProgress.done();
                                util.Msg.success(_this);
                                _this.$refs['editForm'].resetFields();
                                _this.editFormVisible = false;
                                _this.getContents();
                            });
                        });
					}
				});
			},
			//
			addSubmit: function () {
                var _this = this;
				this.$refs.addForm.validate((valid) => {
					if (valid) {
                        util.Msg.confirm(_this , null ,function () {
                            _this.addLoading = true;
                            NProgress.start();
                            let para = Object.assign({}, _this.addForm);
                            addAcct(para).then(function()  {
                                _this.addLoading = false;
                                NProgress.done();
                                util.Msg.success(_this);
                                _this.$refs['addForm'].resetFields();
                                _this.addFormVisible = false;
                                _this.getContents();
                            });
                        });
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
                var _this = this;
				var ids = this.sels.map(item => item.id).toString();
                util.Msg.warning(_this , null ,function () {
                    _this.listLoading = true;
                    NProgress.start();
                    let para = { ids: ids };
                    delAcct(para).then(function() {
                        _this.listLoading = false;
                        NProgress.done();
                        util.Msg.success(_this);
                        _this.getContents();
                    });
                });
			},
			beforUpload:function(){
				this.addLoading = true;
				NProgress.start();
			},
			uploadSuccess:function(){
				var _this = this;
				util.Msg.success(_this , "");
				_this.addLoading = false;
				NProgress.done();
				_this.getContents();
				_this.addFormVisible = false;
			},
			uploadError:function(){
				var _this = this;
				util.Msg.error(_this , "");
				_this.addLoading = false;
				NProgress.done();
				_this.getContents();
				_this.addFormVisible = false;
			},
		},
		mounted() {
			this.getContents();
		}
	}

</script>

<style scoped>

</style>


